<template>
  <div>
    <b-row>
      <b-col md="4">
        <b-card>
          <div
            class="card-title d-flex justify-content-between align-items-center"
          >
            Inbox List
            <div>
              <b-button
                variant="outline-primary"
                size="sm"
                @click="openModal('msgFilter')"
                class="mr-1"
              >
                <feather-icon icon="FilterIcon" size="16" />
                Filter
              </b-button>
              <b-button
                variant="primary"
                size="sm"
                @click="openModal('msgFilter')"
              >
                <feather-icon icon="PlusIcon" size="16" />
                New
              </b-button>
            </div>
          </div>
          <div class="full-heigt">
            <div
              class="msg d-flex align-items-center"
              v-for="msg in messages"
              :id="'msg' + msg.id"
              :key="msg.id"
              @click="detail(msg)"
            >
              <div>
                <b-img
                  src="https://via.placeholder.com/40"
                  class="rounded-circle"
                  alt="profile"
                ></b-img>
              </div>
              <div
                class="d-flex justify-content-between align-items-center ml-1 w-100"
              >
                <div>
                  <h6>{{ msg.sender }}</h6>
                  <span>{{ msg.subject }}</span>
                </div>
                <div>
                  <span>{{ msg.sendDate }}</span>
                </div>
              </div>
            </div>
          </div>
        </b-card>
      </b-col>
      <b-col md="8">
        <b-card title="Inbox Detail" style="padding-bottom: 0.75rem">
          <div class="full-heigt" v-if="detailInbox == false">
            <div class="d-flex justify-content-center align-items-center h-100">
              <span class="text-center">
                <feather-icon
                  size="80"
                  icon="MessageSquareIcon"
                  class="mr-50"
                />
                <br />
                <h2>Select an Inbox!</h2>
              </span>
            </div>
          </div>
          <div class="full-heigt" v-else>
            <table class="semi-bold">
              <tr>
                <td width="60">From</td>
                <td>: {{ detailInboxData.sender }}</td>
              </tr>
              <tr>
                <td>Type</td>
                <td>: {{ detailInboxData.subject }}</td>
              </tr>
              <tr>
                <td>Date</td>
                <td>: {{ detailInboxData.sendDate }}</td>
              </tr>
            </table>
            <p class="mt-1">{{ detailInboxData.message }}</p>
          </div>
        </b-card>
      </b-col>
    </b-row>

    <b-modal id="msgFilter" title="Filter Inbox" size="md" hide-footer>
      <form @submit.prevent>
        <b-form-group label="Sender">
          <b-form-select>
            <b-form-select-option value="1">Type 1</b-form-select-option>
          </b-form-select>
        </b-form-group>
        <b-form-group label="Type">
          <b-form-select>
            <b-form-select-option value="1">Type 1</b-form-select-option>
          </b-form-select>
        </b-form-group>
        <b-form-group label="Date">
          <b-form-input type="date" placeholder="Enter Date"></b-form-input>
        </b-form-group>
        <div class="text-right">
          <b-button type="submit" variant="primary">Submit</b-button>
        </div>
      </form>
    </b-modal>
  </div>
</template>

<style>
.full-heigt {
  height: 57vh;
  overflow-y: auto;
}

.msg {
  border-bottom: 1px solid #e0e0e0;
  padding: 10px 10px;
  cursor: pointer;
}

.msg:hover {
  background-color: #f5f5f5;
}
</style>

<script>
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";

import {
  BCard,
  BCardText,
  BLink,
  BCol,
  BRow,
  BImg,
  BButton,
  BModal,
  BFormGroup,
  BFormInput,
  BFormSelect,
  BFormSelectOption,
} from "bootstrap-vue";

export default {
  components: {
    ToastificationContent,
    BFormGroup,
    BFormInput,
    BModal,
    BButton,
    BCard,
    BCardText,
    BLink,
    BCol,
    BRow,
    BImg,
    BFormSelect,
    BFormSelectOption,
  },

  data() {
    return {
      detailInbox: false,
      detailInboxData: {},

      messages: [
        {
          id: 1,
          sender: "Rizki Maulana",
          subject: "Goals Setting",
          sendDate: "12/12/2020",
          message: "Goals Setting",
        },
        {
          id: 2,
          sender: "Hari Agus Permana",
          subject: "Goals Setting",
          sendDate: "12/12/2020",
          message: "Goals Setting",
        },
        {
          id: 3,
          sender: "Fadly Nurahman",
          subject: "Goals Setting",
          sendDate: "12/12/2020",
          message: "Goals Setting",
        },
      ],
    };
  },

  mounted() {},

  methods: {
    openModal(modal) {
      this.$root.$emit("bv::show::modal", modal);
    },

    detail(msg) {
      this.detailInbox = true;
      this.detailInboxData = msg;
    },
  },
};
</script>